import { type FC, useState, useEffect, useCallback } from 'react'
import { Button, Badge, List, Space } from 'antd'
import useDeepCompareEffect from './hooks/use-deep-compare-effect'
import useUpdateEffect from './hooks/use-update-effect'
import useAsyncEffect from './hooks/use-async-effect'
// import { useUpdateEffect } from 'ahooks'

const data = [
  '1. deps',
  '2. deps[]',
  '3. deps,',
  '4. deps,',
  '5. mountdeps',
  '6. useEffect ',
  '7. useEffect',
  '8. useEffectasync function useAsyncEffect',
]

const UseEffect: FC = () => {
  const [count, setCount] = useState(1)
  const [arr, setArr] = useState<number[]>([])
  // 1. deps',
  useEffect(() => {
    console.log('UseEffect')
  })

  // deps[]', componentDidMountrender
  useEffect(() => {
    console.log('UseEffect componentDidMount')
  }, [])

  // 3. deps,',
  useEffect(() => {
    console.log('arr changed')
  }, [arr])

  // 4. deps,,
  useDeepCompareEffect(() => {
    console.log('useDeepCompareEffect')
  }, [arr])
  // 5. mountdeps,
  useUpdateEffect(() => {
    console.log('useUpdateEffect')
  }, [count])
  // 6. useEffect 
  useEffect(() => {
    document.querySelector('#btn')?.addEventListener('click', function () {
      console.log('click, ')
    })
  }, [count])

  const handleBtnClick = useCallback(() => {
    console.log('click, ')
  }, [])

  useEffect(() => {
    document.querySelector('#btn1')?.addEventListener('click', handleBtnClick)
    return () => {
      document
        .querySelector('#btn1')
        ?.removeEventListener('click', handleBtnClick)
    }
  }, [count])
  // todo: ts
  // 7. useEffecttststs
  // useEffect(() => {
  //   return 1
  // }, [])
  // 8. useEffectasync function useAsyncEffect

  useAsyncEffect(async () => {
    const a = await Promise.resolve(1)
    console.log('useAsyncEffect', a)
  }, [count])

  /**
   * arruseEffect
   * FC
   */
  const changeArrValue = () => {
    arr.push(2)
    console.log('changeArrValue', arr)
    setArr(arr)
  }
  const changeArrAddress = () => {
    setArr(arr.concat(1))
    // 
    // setArr([...arr, 1])
  }
  const onlyChangeArrAddress = () => {
    setArr([...arr])
  }

  const handleClick = () => {
    setCount(count + 1)
  }

  return (
    <Space direction="vertical">
      <Space>
        <Badge count={count}>
          <Button onClick={handleClick}>count + 1</Button>
        </Badge>
      </Space>
      <div>
        <Space direction="vertical">
          <Space>
            <Button onClick={changeArrValue}>changeArrValue</Button>
            <Button onClick={changeArrAddress}>changeArrAddress</Button>
            <Button onClick={onlyChangeArrAddress}>onlyChangeArrAddress</Button>
            <Button id="btn">click, </Button>
            <Button id="btn1" type="primary">
              click, 
            </Button>
          </Space>
          <div>arr: {JSON.stringify(arr)}</div>
        </Space>
      </div>
      <List
        size="large"
        header={<div>useEffect </div>}
        bordered
        dataSource={data}
        renderItem={(item) => <List.Item>{item}</List.Item>}
      />
    </Space>
  )
}

export default UseEffect
